body {
  background: #000 url("../images/site-bg.jpg") repeat-x center 40px; }

.wrap {
  max-width: 1200px;
  margin: 0 auto;
  position: relative; }
  @media (min-width: 0) and (max-width: 640px) {
    .wrap {
      max-width: 750px; } }

.home__logo {
  position: absolute;
  width: 196px;
  height: 89px;
  left: 62px;
  top: 20px; }
  .home__logo a {
    display: block; }
  .home__logo img {
    max-width: 100%;
    display: block;
    margin: 0; }

.main-title {
  width: 729px;
  height: 200px;
  margin: 0 auto;
  padding-top: 82px;
  position: relative; }
  @media (min-width: 641px) and (max-width: 992px) {
    .main-title {
      width: 583.2px;
      height: 160px;
      padding-top: 140px; } }
  .main-title__bg {
    max-width: 100%;
    display: block;
    margin: 0;
    position: relative;
    z-index: 0; }
  .main-title__effect-text {
    width: 100%;
    position: absolute;
    top: 82px;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    animation-name: glow;
    animation-duration: 2000ms;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    mix-blend-mode: color-dodge; }
    @media (min-width: 641px) and (max-width: 992px) {
      .main-title__effect-text {
        top: 140px; } }

@keyframes glow {
  0%,
  100% {
    opacity: 0; }
  50% {
    opacity: 1; } }

.countdown {
  width: 619px;
  height: 342px;
  margin: 0 auto;
  padding-top: 14px;
  position: relative; }
  .countdown__bg {
    max-width: 100%;
    display: block;
    margin: 0;
    position: relative;
    z-index: 0; }
  .countdown__number {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 100;
    transform: translate3d(-65%, -45%, 0);
    font-size: 0; }
    .countdown__number.is-odd {
      transform: translate3d(-50%, -45%, 0); }
  .countdown__slot {
    width: 90px;
    height: 148px;
    display: inline-block;
    overflow: hidden; }
    .countdown__slot img {
      max-width: 100%;
      display: block;
      margin: 0; }
    .countdown__slot:nth-child(1) {
      margin-right: 0; }
