:root {
    font-size: 62.5%;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
    &.is-popup {
        overflow: hidden;
    }
}

body {
    background: #000 url('../images/site-bg.jpg') repeat-x center 40px;
}

.wrap {
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        max-width: 750px;
    }
}

.home {
    &__logo {
        position: absolute;
        width: 196px;
        height: 89px;
        left: 62px;
        top: 20px;
        a {
            display: block;
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
}

.main-title {
    width: 729px;
    height: 200px;
    margin: 0 auto;
    padding-top: 82px;
    position: relative;
    @include susy-breakpoint($s-768, $c12) {
        width: 729px * 0.8;
        height: 200px * 0.8;
        padding-top: 140px;
    }
    &__bg {
        max-width: 100%;
        display: block;
        margin: 0;
        position: relative;
        z-index: 0;
    }
    &__effect-text {
        width: 100%;
        position: absolute;
        top: 82px;
        left: 50%;
        transform: translate3d(-50%,0,0);
        animation-name: glow;
        animation-duration: 2000ms;
        animation-timing-function: ease-in-out;
        animation-fill-mode: forwards;
        animation-iteration-count: infinite;
        animation-direction: alternate;
        mix-blend-mode: color-dodge;
        @include susy-breakpoint($s-768, $c12) {
            top: 140px;
        }
    }
}

@keyframes glow {
    0%,
    100% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

.countdown {
    width: 619px;
    height: 342px;
    margin: 0 auto;
    padding-top: 14px;
    position: relative;
    &__bg {
        max-width: 100%;
        display: block;
        margin: 0;
        position: relative;
        z-index: 0;
    }
    &__number {
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 100;
        transform: translate3d(-65%,-45%,0);
        font-size: 0;
        &.is-odd {
            transform: translate3d(-50%,-45%,0);
        }
    }
    &__slot {
        width: 90px;
        height: 148px;
        display: inline-block;
        overflow: hidden;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
        &:nth-child(1) {
            margin-right: 0;
        }
    }
}